import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    battledex: false,
    selectedPokemon: false,
    isLoading: false,
    showModal: true
  },
  mutations: {
    set_battledex: function (state, payload) {
      state.battledex = payload
    },
    set_selectedPokemon: function(state, pokemon){
      state.selectedPokemon = pokemon
    },
    set_isLoading: function(state, isLoading) {
      state.isLoading = isLoading
    },
    setModalState: function(state, s){
      state.showModal = s
    }
  },
  getters: {
    getModalState: function(state){
      return state.showModal
    },
    get_selectedPokemon: function(state){
      return state.selectedPokemon
    },
    check_battledex: function (state) {
      if (state.battledex != false) {
        return true
      }
      return false
    },
    battledex: function (state) {
      return state.battledex
    },
    select_battledex: function (state) {
      return key => {
        return state.battledex[key]
      }
    },
    isLoading: function(state){
      return state.isLoading
    }
  }
})
