export default {
    computed:{
        battledex(){
            return this.$store.getters['battledex']
        },
        battledex_size(){
            return Object.keys(this.$store.getters['battledex']).length
        },
        getSelectedPokemon(){
            return this.$store.getters['get_selectedPokemon']
        },
        isLoading(){
            return this.$store.getters['isLoading']
        },
        modalState(){
            return this.$store.getters['getModalState']
        }
    },
    methods:{
        select(from, what){
            let s = this.$store.getters[`select_${from}`](what)
            if (s !== undefined) {
                s.key = what;
                return s
            }
            return {slug: what}
        },
        setSelectedPokemon(pokemon){
            let pkmn = this.select('battledex', pokemon)
            this.$store.commit("set_selectedPokemon", pkmn)
        },
        unsetSelectedPokemon(){
            this.$store.commit("set_selectedPokemon", false)
        },
        setModalState: function(state){
            this.$store.commit("setModalState", state)
        },
    }
}