import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: () => import(/* webpackChunkName: "about" */ '../views/Home.vue')
  },
  {
    path:"/dex/:tier",
    name:"tier",
    component:() => import('../views/Tier.vue')
  },
  {
    path:"/pokemon/:tier/:pokemon",
    name:"pokemon",
    component:() => import('../views/Pokemon.vue')
  },
  {
    path: "/breakmyteam",
    name: "breakmyteam",
    component: () => import("../views/BreakMyTeam.vue")
  },
  {
    path: "/breakmyteam/shared/:tier/:id",
    name: "sharemyteam",
    component: () => import("../views/ShareMyTeam.vue")
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
